import Vue from 'vue';
import { Breadcrumb, BreadcrumbItem } from 'element-ui';
import * as pathToRegexp from 'path-to-regexp';
import { urlToList } from '../utils/pathTools';
Vue.use(Breadcrumb);
Vue.use(BreadcrumbItem);
export function getBreadcrumb(breadcrumbNameMap, url) {
    let breadcrumb = breadcrumbNameMap[url];
    if (!breadcrumb) {
        Object.keys(breadcrumbNameMap).forEach(item => {
            if (pathToRegexp(item).test(url)) {
                breadcrumb = breadcrumbNameMap[item];
            }
        });
    }
    return breadcrumb || {};
}
export default Vue.extend({
    props: {
        title: String,
        logo: String,
        action: String,
        content: String,
        extraContent: String,
        breadcrumbList: Array,
        breadcrumbNameMap: Object,
        breadcrumbSeparator: String,
        linkElement: {
            type: String,
            default: 'router-link'
        }
    },
    methods: {
        renderItem(linkElement, href, title) {
            return this.$createElement(linkElement, {
                props: linkElement !== 'a' ? { to: href } : {},
                attrs: linkElement === 'a' ? { href: href } : {}
            }, title);
        },
        conversionFromProps() {
            const { breadcrumbList, breadcrumbSeparator, linkElement } = this;
            return this.$createElement('el-breadcrumb', {
                class: 'breadcrumb',
                props: {
                    separator: breadcrumbSeparator
                }
            }, breadcrumbList.map(item => {
                return this.$createElement('el-breadcrumb-item', {
                    key: item.title
                }, [
                    item.href
                        ? this.renderItem(linkElement, item.href, item.title)
                        : item.title
                ]);
            }));
        },
        conversionFromLocation(routerPath, breadcrumbNameMap) {
            const { breadcrumbSeparator, linkElement } = this;
            // Convert the url to an array
            const pathSnippets = urlToList(routerPath);
            // Loop data mosaic routing
            const extraBreadcrumbItems = pathSnippets.map((url, index) => {
                const currentBreadcrumb = getBreadcrumb(breadcrumbNameMap, url);
                const isLinkable = index !== pathSnippets.length - 1 && currentBreadcrumb.component;
                if (currentBreadcrumb.name && !currentBreadcrumb.hideInBreadcrumb) {
                    return this.$createElement('el-breadcrumb-item', {
                        key: url
                    }, [
                        this.renderItem(isLinkable ? linkElement : 'span', url, currentBreadcrumb.name)
                    ]);
                }
                return this.$createElement();
            });
            // Add home breadcrumbs to your head
            extraBreadcrumbItems.unshift(this.$createElement('el-breadcrumb-item', {
                key: 'home'
            }, [this.renderItem(linkElement, '/', '首页')]));
            return this.$createElement('el-breadcrumb', {
                class: 'breadcrumb',
                props: {
                    separator: breadcrumbSeparator
                }
            }, extraBreadcrumbItems);
        },
        conversionBreadcrumbList() {
            const { breadcrumbList, breadcrumbSeparator, breadcrumbNameMap } = this;
            const routerPath = this.$route.path;
            if (breadcrumbList && breadcrumbList.length) {
                return this.conversionFromProps();
            }
            if (routerPath) {
                return this.conversionFromLocation(routerPath, breadcrumbNameMap);
            }
        }
    },
    render(h) {
        const { logo, title, action, content, extraContent } = this;
        return h('div', {
            class: 'page-header'
        }, [
            this.conversionBreadcrumbList(),
            h('div', {
                class: 'detail'
            }, [
                (logo || this.$slots.logo) &&
                    h('div', { class: 'logo' }, logo || this.$slots.logo),
                h('div', { class: 'main' }, [
                    h('div', { class: 'row' }, [
                        (title || this.$slots.title) &&
                            h('h1', { class: 'title' }, title || this.$slots.title),
                        (action || this.$slots.action) &&
                            h('div', { class: 'action' }, action || this.$slots.action)
                    ]),
                    h('div', { class: 'row' }, [
                        (content || this.$slots.content) &&
                            h('div', { class: 'content' }, content || this.$slots.content),
                        (extraContent || this.$slots.extraContent) &&
                            h('div', { class: 'extra-content' }, extraContent || this.$slots.extraContent)
                    ])
                ])
            ])
        ]);
    }
});



